$player-margin: 10px
$card-width: 110px
$turn-color: #666

@keyframes pulse
  0%
    background: white
  100%
    background: #2e3236
    color: white


.player
  position: absolute
  background: #FFF
  border: 1px solid #CCC
  padding: 5px
  border-radius: 20px
  overflow: hidden
  padding: 12px
  min-width: 300px
  transition: .2s
  //box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2)


  &.player-turn
    border: 1px solid black
    background: #2e3236
    color: white

    //color: white
    //box-shadow: 0 1px 8px rgba(0, 0, 0, 0.8)


  &.p1
    left: $player-margin
    top: 50vh
    transform: translateY(-50%)


  &.p2
    top: $player-margin
    left: 50vw
    transform: translateX(-50%)

  &.p3
    right: $player-margin
    top: 50vh
    transform: translateY(-50%)

  &.p0
    bottom: $player-margin
    left: 50vw
    transform: translateX(-50%)

.player-hand
  padding: 0px 0px
  margin: 0
  margin-bottom: 0
  padding-bottom: 0

.player-card
  vertical-align: bottom
  width: $card-width
  transform: translateY(0px)
  transition: 0.15s
  border-radius: 7px
  border: 1px solid #DDD
  margin-bottom: 1px

  &:not(:first-child)
    margin-left: -$card-width*0.7

  &.visible
    -webkit-filter: grayscale(0%)
    border-radius: 0
    border: 0

.controllable .player-card:hover
    cursor: pointer
    transition: 0.02s
    transform: translateY(-15px)


.player-header
  position: relative
  padding: 10px 10px
  font-weight: bold

  .dummy
    font-weight: normal

  .declarer
    font-weight: normal

  .bot
    font-weight: 900
    margin-right: 10px
    position: absolute
    right: 0
